.pluses {
  padding-top: 40px;
  padding-bottom: 73px;
  padding-left: 20px;
  padding-right: 10px;

  @media (min-width: 768px) {
    padding-left: @padding_on_768;
    padding-right: @padding_on_768;
  }

  @media (min-width: 1440px) {
    padding-left: @padding_on_1440;
    padding-right: @padding_on_1440;
  }
}

.pluses__list {
  display: grid;

  padding: 0;
  margin: 0;

  @media (min-width: 768px) {
    grid-template-columns: repeat(3, auto);
    justify-content: space-between;
  }

  @media (min-width: 1440px) {
    justify-content: center;
  }
}

.pluses__item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  margin-bottom: 30px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    flex-direction: column;
    margin-bottom: 0;
  }

  @media (min-width: 1440px) {
    &:nth-child(1),
    &:nth-child(3n+1) {
      margin-right: 80px;
    }

    &:nth-child(2),
    &:nth-child(3n+2) {
      margin-left: 80px;
      margin-right: 80px;
    }

    &:nth-child(3),
    &:nth-child(3n+3) {
      margin-left: 80px;
    }
  }
}

.plus__place-description {
  display: flex;
  flex-direction: column;

  margin-left: 25px;

  @media (min-width: 768px) {
    margin-left: 0;
  }
}

.plus__name {
  font-family: "Oswald Medium", "Arial Narrow", sans-serif;
  color: #000000;
  font-size: 20px;
  line-height: 30px;

  margin: 0;
  margin-top: 5px;

  @media (min-width: 768px) {
    font-size: 29px;
    line-height: 43px;
  }
}

.plus__img {
  height: 100px;
  width: 100px;
  
  margin-right: 25;

  @media (min-width: 768px) {
    height: 80px;
    width: 80px;

    margin-right: 0;
  }
}

.plus__description {
  max-width: 158px;

  font-family: "Oswald Regular", "Arial Narrow", sans-serif;
  font-size: 12px;
  line-height: 18px;
  color: #959595;

  margin: 0;

  @media (min-width: 768px) {
    max-width: 220px;

    margin-top: 28px;

    font-size: 15px;
    line-height: 22px;
  }
}
