@font-face {
  font-family: "MA Medium";
  font-display: swap;
  src: url("../fonts/MontserratAlternates-Medium.woff2") format("woff2"), url("../fonts/MontserratAlternates-Medium.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "MA Regular";
  font-display: swap;
  src: url("../fonts/MontserratAlternates-Regular.woff2") format("woff2"), url("../fonts/MontserratAlternates-Regular.woff") format("woff");
  font-weight: normal;
}
@font-face {
  font-family: "MA Bold";
  font-display: swap;
  src: url("../fonts/MontserratAlternates-Bold.woff2") format("woff2"), url("../fonts/MontserratAlternates-Bold.woff") format("woff");
  font-weight: bold;
}
@font-face {
  font-family: "Oswald Medium";
  font-display: swap;
  src: url("../fonts/Oswald-Medium.woff2") format("woff2"), url("../fonts/Oswald-Medium.woff") format("woff");
  font-weight: 700;
}
@font-face {
  font-family: "Oswald Regular";
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
  font-weight: normal;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.burger__menu {
  background-color: transparent;
  border: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .burger__menu {
    display: none;
  }
}
.burger__img {
  fill: white;
}
.slogan {
  position: relative;
  width: inherit;
  height: inherit;
}
.slogan__img {
  position: absolute;
  width: 292px;
  height: 107px;
  top: 92px;
}
@media (min-width: 768px) {
  .slogan__img {
    width: 750px;
    height: 284px;
    top: 50%;
    transform: translateY(-50%);
