.searcher {
  position: relative;
  flex-grow: 1;
  max-width: 197px;
  height: 32px;
  display: flex;
  flex-direction: row;
}
@media (min-width: 768px) {
  .searcher {
    height: 48px;
    max-width: 600px;
    margin-left: 26px;
    margin-right: 30px;
    margin-bottom: 17px;
  }
}
@media (min-width: 1440px) {
  .searcher {
    margin-left: 20px;
    margin-right: 80px;
  }
}
.searcher__searcher {
  position: absolute;
  top: 7px;
  left: 7px;
  border: none;
  background-color: transparent;
  padding: 0;
}
.searcher__searcher__img {
  position: absolute;
  width: 11px;
  height: 16.47px;
}
@media (min-width: 768px) {
  .searcher__searcher__img {
    width: 19px;
    height: 30px;
    margin-left: 14px;
  }
}
.searcher__searcher__img--path {
  fill: white;
}
.searcher__searcher__img--line {
  stroke: white;
}
.searcher__input {
  outline: none;
  width: 100%;
  font-family: "MA Medium", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  background-color: transparent;
  color: white;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
  padding-left: 25px;
  border: 2px solid white;
  box-sizing: border-box;
  border-radius: 10px;
  font-size: 13px;
  line-height: 16px;
}
.searcher__input::placeholder {
  color: white;
  opacity: 0.75;
}
.searcher__input:hover {
  border: 2px solid white;
}
.searcher__input:focus {
  border: 2px solid white;
  background-color: rgba(0, 255, 87, 0.6);
}
.searcher__input:active {
  border: 2px solid white;
}
@media (min-width: 768px) {
  .searcher__input {
    border: 3px solid white;
    font-size: 20px;
    line-height: 24px;
    padding-left: 48px;
    box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.25);
  }
  .searcher__input:hover {
    border: 3px solid white;
  }
  .searcher__input:focus {
    border: 3px solid white;
  }
  .searcher__input:active {
    border: 3px solid white;
  }
}
.searcher__button {
  display: none;
}
@media (min-width: 1440px) {
  .searcher__button {
    display: block;
    position: absolute;
    right: 0;
    width: 60px;
    height: 48px;
    background-color: #ff0000;
    border-radius: 0 10px 10px 0;
    border: 0;
    font-family: "MA Medium", "Arial", sans-serif;
    font-size: 13px;
    line-height: 16px;
    color: white;
  }
}
.row {
